import { MovieElement } from "./movieElement"

export class MoviesList {
    
    static url = 'https://movie-app-b6690-default-rtdb.firebaseio.com/movieList.json'
    static apiKey = '2vOCQ1FAw6pAvfhXbj0lHyTsIdC9aP8DkAHJceEb'

    static create(movie){
        return fetch(this.url, {
            method: 'POST',
            body: JSON.stringify(movie),
            headers: {
                'Content-Type': 'application/json'
            }
        })
        .then(response => response.json())
        .then(response => {
            movie.id = response.name
            return movie
        })
        .then(addToLocalStorage)
        .then(MoviesList.renderList)
    }

    static fetch(){
        return fetch(`${this.url}?auth=${this.apiKey}`)
        .then(response => response.json())
        .then(movies => {
            return movies ? Object.keys(movies).map(key => ({
                ...movies[key],
                id: key
            })) : []
        })
    }
    static updateStatus(e) {
        e.preventDefault()
        const target = e.target
        if ( target.classList[0] !== 'movie__status' ) {return ''}
        return fetch(`https://movie-app-b6690-default-rtdb.firebaseio.com/movieList/${target.id}.json?auth=${MoviesList.apiKey}`)
        .then(response => response.json())
        .then(movie => {
            movie.status = !movie.status
            return movie
        })
        .then(content => {
            return fetch(`https://movie-app-b6690-default-rtdb.firebaseio.com/movieList/${target.id}.json?auth=${MoviesList.apiKey}`, {
                method: 'PUT',
                body: JSON.stringify(content),
                headers: {'Content-Type': 'application/json'}
            })
        })
        .then(content => {
            localStorage.clear()
            MoviesList.renderList()
        })
    }

    static refresh(e) {
        e.preventDefault()
        localStorage.clear()
        MoviesList.renderList()
    }

    static searchInList() {
        const input = document.querySelector('#search-input').value.trim().toLowerCase()
        MoviesList.getMoviesWithFilters(input)
    }

    static getMoviesWithFilters(search = '', filters = []) {
        let all = getMoviesFromLocalStorage()
        if (search.length > 0) {
            all = all.filter(key => key.title.toLowerCase().includes(search) || key.description.toLowerCase().includes(search))
        }
        if (filters.length > 0) {
            console.log(filters)
            all = all.filter(movie => {
                return filters.every(f => {
                    return movie[f[0]] === f[1]
                })
            })
        }
        MoviesList.renderList(all)
    }

    static renderList(movies = '') {
        let all = movies ? movies : getMoviesFromLocalStorage()
        if (all.length === 0 && !movies) {
            MoviesList.fetch()
            .then(movieList => movieList.forEach(addToLocalStorage))
            .then(MoviesList.renderList)
        }
        const html = all.length
        ? all.map(toBlock).join('')
        : '<div>Нет фильмов</div>'
        document.querySelector('#list').innerHTML = html
    }
}

function addToLocalStorage(movie){
    const all = getMoviesFromLocalStorage()
    all.push(movie)
    localStorage.setItem('movies', JSON.stringify(all))
}

function getMoviesFromLocalStorage(){
    return JSON.parse(localStorage.getItem('movies') || '[]')
}

function toBlock(movie) {
    return new MovieElement(movie).toHTML()
}