import { generateForm, generateModal, fromObjectToAttribute } from '../utils'
import { MoviesList } from './moviesList'

export class AddMovie {

    constructor(selector){
        this.$el = document.querySelector(selector)

        this.createModal()
    }

    createModal() {
        const modal = generateModal(generateForm())
        const attrs = {'data-bs-toggle': 'modal', 'data-bs-target': '#open-modal'}
        const event = fromObjectToAttribute(document.querySelector('#add-movie'), attrs)
        this.$el.addEventListener('submit', this.add.bind(this))
        this.$el.append(modal)
    }

    add(event) {
        event.preventDefault()

        const target = event.target
        const newMovie = this.check(target)
        if (!newMovie){
            this.clear(target)
            return ''
        }

        MoviesList.create(newMovie)
        this.clear(target)
    }

    check(target) {
        const title = target.title.value.trim()
        const year = target.year.value
        const url = target.url.value.trim()
        const description = target.description.value.trim()
        const genre = target.genre.value
        const status = false
        if (title === '' || description === '' || genre === 'Выберите жанр' || url === '') {
           return false 
        }
        return {title, year, url, description, genre, status}
    }
    
    clear(target) {
        target.title.value = ''
        target.url.value = ''
        target.year.value = 2000
        target.description.value = ''
        target.genre.selectedIndex = 0
    }
}