import { MoviesList } from "./moviesList";
import { FilterMovie } from "./filterMovie";
import { AddMovie } from "./addMovie";

export class App {
    
    init() {
        window.addEventListener('load', MoviesList.renderList())
        document.querySelector('#search-btn').addEventListener('click', MoviesList.searchInList)
        document.querySelector('#refresh').addEventListener('click', MoviesList.refresh)
        document.querySelector('.list').addEventListener('click', MoviesList.updateStatus)
        new FilterMovie('#filter')
        new AddMovie('#modal-window')
    }
}