import { generateFilter } from "../utils"
import { MoviesList } from "./moviesList"

export class FilterMovie {
    constructor(selector) {
        this.$el = document.querySelector(selector)

        this.createFilter()
    }

    createFilter() {
        const filterBlock = generateFilter()
        this.$el.addEventListener('submit', this.getFilters.bind(this))
        this.$el.append(filterBlock)
        document.querySelector('#clear').addEventListener('click', this.clear)
        document.querySelector('#filter-header').addEventListener('click', this.open)
    }

    open() {
        const show = document.querySelector('.filter-form')
        show.classList.contains('show') ? show.classList.remove('show') : show.classList.add('show')
    }

    getFilters(e) {
        e.preventDefault()
        const formData = new FormData(e.target)
        let filters = []
        for (let pair of formData.entries()){
            if (pair[1] === '') {continue}
            if (pair[0] === 'status') {pair[1] = true}
            if (pair[0] === 'year') {pair[1] = +pair[1]}
            filters.push(pair)
        }
        MoviesList.getMoviesWithFilters('', filters)
    }

    clear() {
        const dropdown = document.querySelector('.form-select')
        const search = document.querySelector('#search-input')
        search.value = ''
        dropdown.selectedIndex = 0
        MoviesList.renderList()
    }
}